* {
    padding: 0;
    margin: 0;
    border: 0;
}
*,*:before,*:after {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
:focus, :active {outline: none;}
a:focus, a:active {outline: none;}
header, main, footer, section, nav, div, aside {display: block;}
html, body {
    width: 100%;
    height: 100%;
    font-size: 100%;
    line-height: 1;
    font-size: 14px;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}
input, button, textarea, select {font-family: inherit;}
input::-ms-clear {display: none;}
button {cursor: pointer;}
button::-moz-focus-inner {padding: 0; border: 0;}
a, a:visited {text-decoration: none;}
a:hover {text-decoration: none;}
ul, li {list-style: none;}
img {vertical-align: top;}
h1, h2, h3, h4, h5, h6 { font-size: inherit; font-weight: 400;}

@font-face {
    font-family: Stolzl;
    src: url('./fonts/stolzl_bold.otf');
    src: url('./fonts/stolzl_book.otf');
    src: url('./fonts/stolzl_light.otf');
    src: url('./fonts/stolzl_medium.otf');
    src: url('./fonts/stolzl_regular.otf');
    src: url('./fonts/stolzl_thin.otf');
}

body {
    font-family: 'Stolzl', sans-serif;
    color: white;
    background-color: #0C1D37;
}

button {
