}

.d-none {
    display: none;
}

.text-header {
    font-size: 32px;
}

.text-align-center {
    text-align: center;
}

/* ---------------------------------------header--------------------------------------- */
.header {
    min-height: 110px;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    border-bottom: 1px solid #FFFFFF;
}

.container-header {
    max-width: 1280px;
    margin: 0 auto;
    padding: 25px;
}

.container-header-one {
    max-width: 100%;
    margin: 0 auto;
    justify-content: space-between;
}

.logo {
    flex: 0 1 25%;
}

.logo-text1 {
    font-size: 48px;
    font-weight: 700;
}

.logo-text1 img + p {
    margin-left: 5px;
}

.logo-text2 {
    font-size: 12px;
    line-height: 100%;
    font-weight: 500;
    margin-left: 5px;
}

.container-list {
    flex: 0 1 65%;
}

.list {
    justify-content: space-between;
}

.list li {
    display: flex;
    align-items: center;
    font-size: 15px;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.03em;
}

.list li img {
    margin-right: 7px;
}

.list-basket {
    background-color: #FF7A00;
    padding: 2%;
}

.list-basket span {
    margin-left: 5px;
}

.search {
    cursor: pointer;
}

.search:hover {
    color: #FF7A00;
}

.white {
    color: white;
}

.white-bask {
    color: white;
    display: flex;
    align-items: center;
}

.white:hover {
    color: #FF7A00;
}

/* ----------------menu-burger---------------- */
.menu-burger {
    width: 100%;
    display: none;
    margin-top: 10px;
}

.menu {
    margin-left: 20px;
    cursor: pointer;
}

.menu img {
    width: 150%;
}

.box-menu-burger {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}

.menu-burger-absolute {
    position: absolute;
    width: 100%;
    background-color: black;
    margin: 110px 0 0 0;
}

.burger-list {
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.burger-list li {
    width: 100%;
    height: 60px;
    text-transform: uppercase;
    border-bottom: 1px solid #3B4C64;
    display: flex;
    justify-content: center;
    align-items: center;
}

.menu-orange {
    color: #FF7A00;
}

.m-right-5 {
    margin-right: 5px;
}

@media (max-width: 950px) {
    .media-menu {
        max-width: none;
    }
    .container .container-list {
        display: none;
    }
    .media-menu .menu-burger {
        display: flex;
        justify-content: flex-end;
    }
    .media-menu .search {
        text-transform: uppercase;
        color: #FF7A00;
    }
}
@media (max-width: 767px) {
    .media-menu .container-header-one {
        flex-direction: column;
    }
    .media-menu .menu-burger {
        justify-content: center;
    }
    .media-menu .menu-burger-absolute {
        margin: 125px 0 0 0;
    }
}
@media (max-width: 230px) {
    .media-menu .menu-burger-absolute {
        margin: 135px 0 0 0;
    }
}

